@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;600;700;900&display=swap');

body, .navbar-hero-header, .navbar-hero-navbar, .navbar-hero-section, .navbar-hero-content, .navbar-hero-links li a, .navbar-hero-login, .navbar-hero-cta {
  font-family: 'Poppins', Arial, sans-serif;
}

.navbar-hero-header {
  width: 100%;
  background: linear-gradient(90deg, #1a1a2e 60%, #16213e 100%);
  color: #fff;
  box-shadow: 0 2px 8px rgba(0,0,0,0.12);
}

.navbar-hero-navbar {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 1.2rem 3rem;
}

.navbar-hero-logo {
  font-size: 2.2rem;
  font-weight: 900;
  letter-spacing: 2px;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.navbar-hero-links {
  display: flex;
  gap: 2.5rem;
  list-style: none;
  margin: 0;
  padding: 0;
}

.navbar-hero-links li a {
  color: #fff;
  text-decoration: none;

  font-size: 1.1rem;
  letter-spacing: 1px;
  transition: color 0.2s;
  position: relative;
}
.navbar-hero-links li a::after {
  content: '';
  display: block;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, #00c6ff, #ffb347);
  transition: width 0.3s;
  position: absolute;
  left: 0;
  bottom: -4px;
}
.navbar-hero-links li a:hover::after {
  width: 100%;
}
.navbar-hero-links li a:hover {
  color: #00c6ff;
}

.navbar-hero-login {
  background: transparent;
  color: #00c6ff;
  border: 2px solid #00c6ff;
  border-radius: 6px;
  padding: 0.5rem 1.5rem;
  font-weight: 700;
  cursor: pointer;
  font-size: 1.1rem;
  transition: background 0.2s, color 0.2s;
}
.navbar-hero-login:hover {
  background: #00c6ff;
  color: #fff;
}

.navbar-hero-section {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4rem 3rem 3rem 3rem;
  /* background: linear-gradient(90deg, #1a1a2e 60%, #16213e 100%); */
}

.navbar-hero-content {
  max-width: 650px;
}
.navbar-hero-content h1 {
  font-size: 3.2rem;
  margin-bottom: 1.2rem;
  font-weight: 900;
  letter-spacing: 2px;
  background: linear-gradient(90deg, #00c6ff, #ffb347);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  text-fill-color: transparent;
}
.navbar-hero-content p {
  font-size: 1.3rem;
  margin-bottom: 2rem;
  color: #e0e0e0;
  font-weight: 400;
}
.navbar-hero-cta {
  display: inline-block;
  background: linear-gradient(90deg, #00c6ff, #ffb347);
  color: #fff;
  padding: 0.85rem 2.2rem;
  border-radius: 6px;
  font-weight: 700;
  text-decoration: none;
  font-size: 1.1rem;
  box-shadow: 0 2px 8px rgba(0,0,0,0.10);
  transition: background 0.2s;
}
.navbar-hero-cta:hover {
  background: linear-gradient(90deg, #ffb347, #00c6ff);
}
.navbar-hero-image img {
  max-width: 420px;
  width: 100%;
  border-radius: 16px;
  box-shadow: 0 4px 24px rgba(0,0,0,0.18);
}

@media (max-width: 900px) {
  .navbar-hero-section {
    flex-direction: column;
    text-align: center;
    padding: 2rem 1rem;
  }
  .navbar-hero-image img {
    max-width: 100%;
    margin-top: 2rem;
  }
}
